$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

#add-filter-box {
  .ant-form-item-label {
    text-align: left;
  }

  .col-sm-9 {
    padding-left: 0;
  }

  h4 {
    font-size: 18px;
    margin-bottom: 20px;
  }

  #more-setting {
    overflow: hidden;
    // height: 0;
    transition: height 1s;
  }
}

.panel.panel-light {
  border-radius: 0;
  border: none;
  box-shadow: none;
}

.panel.panel-light > .panel-heading {
  position: relative;
  padding: 20px;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2196078431372549);
  border-radius: 0;
  font-weight: 700;
}

.col-sm-offset-3 {
  margin-left: 25%;
}
