$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.box-title {
  font-size: 20px;
  margin: 10px 0;
}

.box-content {
  padding: 10px 0;

  p {
    margin: 0 0 10px;
  }
}

.box-footer {
  margin: 10px 0;
}

.cv-count {
  width: 83px;
  text-align: center;
  vertical-align: middle;
  height: 83px;
  background-color: $primary;
  border-radius: 50%;
  color: #fff;
  padding-top: 15px;
  margin: 5px auto;
}

.cv-count-number {
  font-size: 30px;
  font-weight: 700;
  line-height: 35px;
}
