$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.cv-item {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #e0dddd;
  padding: 24px 0;

  &:last-child {
    padding-bottom: 0;
    border-bottom: none;
  }

  &:first-child {
    padding-top: 0;
  }

  &__img {
    width: 110px;
    height: 122px;
  }

  &__info {
    padding-left: 20px;

    &__top {
      display: flex;
      justify-content: space-between;

      &__name {
        color: $primary;
        font-weight: 700;
      }

      &__group {
        display: flex;

        img {
          width: 20px;
          height: 20px;
        }

        &__date {
          color: #999;
        }
      }
    }

    &__bottom {
      margin: 0;

      &__btn {
        margin-top: 20px;
        padding: 1px 6px;
        font-size: 12px;
        margin-right: 5px;

        &__icon {
          padding-right: 5px;
        }
      }
    }
  }
}

.cv-item-url {
  border: 1px dashed #ccc;
  font-size: 14px;
  width: 100%;
  outline: none;
  padding: 10px;
  overflow-wrap: break-word;
}
