$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

footer {
  margin-top: 20px;
  background-color: #292c2f;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  padding: 30px 0;

  h3 {
    color: #fff;
    font: normal 36px "Cookie", cursive;

    span {
      color: #5383d3;
    }
  }
}

.footer-left {
  width: 40%;
}

.footer-center {
  width: 35%;

  i {
    background-color: #33383b;
    color: #ffffff;
    font-size: 20px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    margin: 10px 15px;
    vertical-align: middle;
  }

  p {
    display: inline-block;
    color: #ffffff;
    vertical-align: middle;
    margin: 0;

    span {
      display: block;
      font-weight: normal;
      font-size: 14px;
      line-height: 2;
    }
  }
}

.footer-left,
.footer-center,
.footer-right {
  display: inline-block;
  vertical-align: top;
}

.footer-links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 0;

  a {
    display: inline-block;
    line-height: 1.8;
    text-decoration: none;
    color: inherit;
  }
}

.footer-right {
  width: 25%;
  text-align: justify;
}

.footer-company-about {
  line-height: 20px;
  color: #92999f;
  font-size: 13px;
  font-weight: normal;
  margin: 0;

  span {
    display: block;
    color: #ffffff;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 20px;
  }
}

.footer-icons {
  margin-top: 25px;

  a {
    display: inline-block;
    width: 35px;
    height: 35px;
    cursor: pointer;
    background-color: #33383b;
    border-radius: 2px;
    font-size: 20px;
    color: #ffffff;
    text-align: center;
    line-height: 35px;
    margin-right: 3px;
    margin-bottom: 5px;
  }
}
