$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.tag-input-wrapper {
  width: 100%;
  height: fit-content;
  min-height: 40px;
  border-radius: 2px;
  border: 1px solid #d9d9d9;
  position: relative;
}

.my-tag {
  box-sizing: border-box;
  color: rgba(0, 0, 0, 0.85);
  font-size: 14px;
  font-variant: tabular-nums;
  line-height: 1.5715;
  list-style: none;
  font-feature-settings: "tnum";
  display: inline-block;
  height: auto;
  margin: 0 8px 0 0;
  padding: 0 7px;
  font-size: 12px;
  line-height: 20px;
  white-space: nowrap;
  background: #fafafa;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  opacity: 1;
  transition: all 0.3s;
}

.tag-input-selections {
  height: auto;
  max-width: 100%;
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  padding: 5px 10px;

  input {
    height: 100%;
    min-height: 30px;
    flex: 1;
    min-width: 30px;
  }
}

.tag-suggestion {
  position: absolute;
  z-index: 100;
  width: 100%;
  background-color: #5897fb;
  color: white;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  cursor: pointer;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}
