$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.panel-dashboard {
  border-top: none;
  border-radius: 0px 0px 4px 4px;

  .header {
    color: #333;
    font-size: 22px;
    line-height: 30px;
    margin-top: 8px;
    margin-bottom: 5px;
  }

  .panel-body {
    padding: 20px 40px !important;
  }

  .card {
    width: 100%;
    height: 100%;
  }
}

.panel-stats {
  .text {
    color: #555;
    font-size: 14px;
    margin-bottom: 5px;
    font-weight: 700;
  }

  .number {
    font-size: 30px;
    font-weight: 600;
  }
}

.border {
  width: 100%;
}

.row.hr-item {
  padding-bottom: 9.7px;
}

.hr-item {
  border-bottom: 1px solid #e1eef4;

  &:last-child {
    border-bottom: none;
  }

  a {
    color: #000;

    &:hover {
      color: $primary;
    }
  }
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.text-insider {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  line-height: 16px;
  max-height: 32px;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.panel-show {
  border: 1px solid #e1eef4;
  background-color: #fcfcfc;
  height: 100%;

  a {
    color: #000;
  }

  h4 {
    font-size: 18px;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  p {
    color: #888;
    margin: 0 0 10px;
  }
}
