$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.panel {
  .panel-heading {
    padding: 10px 15px;
    border-bottom: 1px solid transparent;
    border-top-right-radius: 3px;
    border-top-left-radius: 3px;
  }

  .panel-body {
    padding: 40px 30px 20px;
  }

  .panel-footer {
    background-color: #fff;
    color: #555;
    padding: 20px 30px;
    border-top: 1px solid #ddd;
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;

    .pull-right {
      display: flex;
      justify-content: flex-end;
    }
  }
}

.panel-default > .panel-heading {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd;
}

.panel-default .panel-heading {
  border-color: #e4e4e4;
}

.panel-default {
  border-color: #e4e4e4;
}

.input-group-btn {
  position: relative;
  font-size: 0;
  white-space: nowrap;

  &:last-child > .btn {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
}

.input-group .form-control:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.btn-default {
  background-color: #ddd;
  border: none;

  &:hover {
    background-color: #babcc3;
  }

  &:focus {
    background-color: #ddd;
  }

  &:disabled {
    cursor: not-allowed;
  }
}

.search-company:focus {
  border-color: #666;
  outline: 0;
  box-shadow: none;
}

input[type="file"]:focus {
  outline: none !important;
}

.company-btn {
  align-items: center;
  padding: 0px 30px;
  justify-content: center;
  display: flex;

  .dashed-loading {
    height: 20px !important;

    &::before,
    &::after {
      width: 20px !important;
      height: 20px !important;
    }
  }
}
