$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.detail-campaign {
  .panel-body {
    padding: 20px;
  }

  .campaign-title {
    margin-top: 0;
    font-size: 25px;
  }

  .col-xs-3,
  .col-xs-9 {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  }

  .col-xs-9 {
    width: 75%;
  }

  .col-xs-3 {
    width: 25%;
  }

  h1 small {
    font-size: 65%;
    font-weight: 400;
    line-height: 1;
    color: #777;
  }

  .btn-save-campaign {
    margin-bottom: 10px;
    padding: 10px 20px;
  }

  .campaign-noti {
    font-size: 12px;
    font-style: italic;
    min-height: 17px;
    position: absolute;
    right: 15px;
    bottom: -10px;
  }

  .campaign-noti.not_yet {
    color: #c60511;
  }
}

.btn-lock {
  width: 100%;
  display: block;
}

.search-result {
  .time {
    color: #888;
    font-size: 0.9em;
  }

  .location {
    font-size: 0.85em;
    color: #666;
    padding: 3px 8px;
    border: 1px dashed #b7b7b7;
    border-radius: 10px;
    text-align: center;
    display: inline-block;
    margin-bottom: 5px;
  }

  .location-right {
    text-align: left;
  }

  .message-no-candidate {
    padding: 20px;

    img {
      width: 380px;
      height: 160px;
      margin-bottom: 20px;
    }
  }
}

.experience-input {
  text-align: right;
  padding-right: 55px;
}

.input-suffix {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.input-prefix {
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}
