$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

.ir-modal {
  border-radius: 3px;

  .btn-close {
    position: absolute;
    right: 3px;
    top: 3px;
    cursor: pointer;
    font-size: 18px;
  }

  .modal-body {
    padding: 0;
  }

  .nav.nav-tabs {
    width: 100%;
    padding: 15px 20px 0;
    background-color: #dfdfdf;
    margin-bottom: 0;

    a {
      text-align: center;
      margin-right: 5px;
      min-width: 150px;
      background-color: $primary;
      color: #fff;
    }
  }

  .nav-tabs > a.active {
    color: $primary;
  }

  .nav-tabs > a.active,
  .nav-tabs > a.active:focus,
  .nav-tabs > a.active:hover {
    cursor: default;
    background-color: #fff;
    border: 1px solid #ddd;
    border-bottom-color: transparent;
  }

  ul {
    margin: 0;
  }

  .tab-content {
    padding: 20px;
  }

  .text-note {
    font-size: 15px;
    font-weight: 700;
    margin-bottom: 15px;
  }
}
