$primary: #2557a7;

$c-white: #ffffff;
// color variable
$c-concrete: #f2f2f2;
$c-wild-sand: #f6f6f6;
$c-alabaster: #f7f7f7;
$c-cod-gray: #1a1a1a;
$c-boulder: #757575;
$c-mine-shaft: #333333;
$c-havelock-blue: #6675df;
$c-chambray: #3b5998;
$c-valencia: #d42e29;
$light-grey: #f0f0f0;
$c-royal-blue: #5468d9;
$c-scampi: #626fb0;

// animation variable
$transition-dur: 0.5s;
$transition-fun: ease-out;

#search-jobs {
  .form-group,
  .col-sm-6 {
    display: inline-block !important;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0;
  }

  .col-sm-10 {
    padding-right: 0;
  }
}

.btn-primary {
  background-color: $primary;
  border-color: transparent;
}

.btn-full-width {
  width: 100%;
}

.select__control,
.icon-select__control {
  border-radius: 4px !important;
  min-height: 40px !important;
}

.icon-select__value-container {
  padding-left: 35px !important;
}
